import React from "react";

import './Menu.scss'

export default class Menu extends React.Component {

    render() {
        const { isMobile, menuItems } = this.props;
        console.log(menuItems);

        const menuItemsJSX = menuItems.map((menuItem) => {
            return (
                <li className={`menu__item${isMobile ? ' mobile' : ''}`}>{menuItem}</li>
            )
        });
        return (
            <nav className={`menu${isMobile ? ' mobile' : ''}`}>
                <ul className={`menu__items${isMobile ? ' mobile' : ''}`}>
                    {menuItemsJSX}
                </ul>
            </nav>
        )
    }
}