import React from "react";

import './CurrentUser.scss';
import CurrentUserPhoto from '../../../../Shared/images/current-user.png';

export default class CurrentUser extends React.Component {
    isMobile = this.props.isMobile;
    render() {
        return (
            <div className={`current-user${this.isMobile ? ' mobile' : ''}`}>
                <div className={`current-user__info${this.isMobile ? ' mobile' : ''}`}>
                    <div className={`current-user__name${this.isMobile ? ' mobile' : ''}`}>Superstar</div>
                    <div className={`current-user__email${this.isMobile ? ' mobile' : ''}`}>Superstar@gmail.com</div>
                </div>
                <div className={`current-user__photo${this.isMobile ? ' mobile' : ''}`}>
                    <img src={CurrentUserPhoto} alt=""/>
                </div>
            </div>
        )
    }
}